import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";
import appProductList from '../../../components/page-component/app-product-list/app-product-list.vue';
import appPrice from '../../../components/page-component/goods/app-price.vue';
import appNoGoods from '../../../components/page-component/app-no-goods/app-no-goods.vue';
import uMask from '../../../components/basic-component/u-mask/u-mask.vue';
import { mapState, mapGetters } from "vuex";

export default {
    name: "index",
    data() {
        return {
            currentRoute: this.$platDiff.route(),
            tabbarbool: false,
            tabList: [
                {id:0, name: '购买礼品卡'},
                {id:1, name: '我的礼品卡'}
            ],
            code: '',
            activeTab: '0',
            list: [],
            setting: {},
            listStyle: false,
            loading: false,
            more: false,
            dialog: false,
            msg: '',
            page: 1,
        };
    },
    computed: {
        ...mapState({
            appImg: state => state.mallConfig.__wxapp_img.mall,
            appSetting: state => state.mallConfig.mall.setting,
            tabBarNavs: state => state.mallConfig.navbar.navs,
            platform: function(state) {
                return state.gConfig.systemInfo.platform;
            }
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme'
        }),
        ...mapGetters('mallConfig', {
            getVideo: 'getVideo'
        })
    },
    components: {
        "app-tab-nav": appTabNav,
        'app-product-list': appProductList,
        appPrice,
        appNoGoods,
        "u-mask": uMask
    },
    watch: {
        tabBarNavs: {
            handler: function() {
                this.b();
            },
            immediate: true,
        }
    },

    onShareAppMessage() {
        return this.$shareAppMessage({
            title: this.$children[0].navigationBarTitle,
            path: "/plugins/exchange/list/list",
        });
    },


    onShareTimeline() {
      // 分享朋友圈beta
      return this.$shareTimeline({
        title: this.$children[0].navigationBarTitle,
        query: {} // 此处填写页面的参数
      });
    },

    onLoad(options) { this.$commonLoad.onload(options);

        wx.showShareMenu({
          menus: ['shareAppMessage', 'shareTimeline']
        })

        if(options.tab > 0) {
            this.activeTab = options.tab.toString();
        }
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.$request({
            url: this.$api.exchange.setting,
        }).then(response=>{
            if(response.code == 0) {
                this.setting = response.data;
                this.getList();
            }else {
                uni.showToast({
                    title: response.msg,
                    icon: 'none',
                    duration: 1000
                });
            }
        })
    },
    onShow() {
        if(this.activeTab == '1') {
            this.getList();
        }
    },
    onReachBottom() {
        if(this.more) {
            this.page++;
            this.getMore();
        }
    },
    methods: {
        b() {

            let currentRoute = this.currentRoute;
            for (let i = 0; i < this.tabBarNavs.length; i++) {
                if(currentRoute.includes(this.tabBarNavs[i].url.split('?')[0])) {
                    return this.tabbarbool = true;
                }
            }

            return  this.tabbarbool = false;
        },
        toIndex() {
            uni.navigateTo({
                url: '/plugins/exchange/index/index'
            })
        },
        getMore() {
            let that = this;
            if(this.loading) {
                return false
            }
            this.loading = true;
            this.more = false;
            that.$request({
                url: that.activeTab == '0' ? that.$api.exchange.list : that.$api.exchange.me_list,
                data: {
                    page: this.page
                }
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                this.loading = false;
                if(response.code == 0) {
                    that.list = that.list.concat(response.data.list);
                    if(response.data.list.length == response.data.pagination.pageSize) {
                        this.more = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
        jump(data) {

            if (data.video_url && this.getVideo == 1) {

                uni.navigateTo({
                    url: `/pages/goods/video?goods_id=${data.id}&sign=exchange`
                });






            } else {
                uni.navigateTo({
                    url: `/plugins/exchange/goods/goods?goods_id=${data.id}`
                });
            }







        },
        apply(goods) {
            uni.navigateTo({
                url: '/plugins/exchange/gift/gift?code=' + goods.code
            })
        },
        tabStatus(e) {
            if(this.loading) {
                return false
            }
            this.list = [];
            this.page = 1;
            this.activeTab = e.currentTarget.dataset.id;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList();
        },
        getList() {
            let that = this;
            if(this.loading) {
                return false
            }
            this.loading = true;
            this.page = 1;
            this.more = false;
            that.$request({
                url: that.activeTab == '0' ? that.$api.exchange.list : that.$api.exchange.me_list
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                this.loading = false;
                if(response.code == 0) {
                    that.list = response.data.list;
                    if(that.list.length == response.data.pagination.pageSize) {
                        this.more = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
    }
}
